@import "imports/reset", "imports/variables";



/*HEADER*/

header .menu_ordenador ul li a{ /*Le damos collor blanco a las redirecciones del nav*/
  color: $blanco;
}

header img{ /*Un valor maximo a la imagen del logo*/
  max-width: 120px;
}
.boton{  /*Seleccionamos el resgistrarse y le creamos la caja con su color y sus estilos*/
  background: $boton-registrarse;
  border-radius: 5px;
  text-align: center;
  margin-right: 15px;
  padding: 7px;
}

.menu_ordenador>ul li{  /*Ponemos todos los li en la misma linea y creamos separacion entre cada uno de ellos*/
  padding: 15px;
  display: inline-block;
}

.section{ /*Le damos color de fondo ,tamaÃ±o atodo lo que es el encabezado y lo alineamos con flex.*/
  height: 63px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $negro;
}
.menu_hamburguesa{
  background-color: transparent;
  color: $blanco;
  display: none;
  cursor: pointer;
  font-size: 30px;
  margin-right: 20px;
}

.menu_movil{
  display: none;
  background-color: $negro;
}
.menu_movil ul{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.menu_movil li>a{
  color: $blanco;
  font-size: 15px;
  margin: 10px;
}



/*MAIN*/

main{
  width: 100%;
  height: 600px;
  background: linear-gradient(230.2deg, rgba(24, 95, 202, 0.08) 0%, rgba(24, 95, 202, 0.18) 13.54%,
          rgba(24, 95, 202, 0.28) 30.75%, rgba(24, 95, 202, 0.7) 76.6%, #185FCA 107.35%);
}
label{
  padding-left:44px ;
  padding-top: 20px;
}
main div>h1{
  text-align: center;
}

section{
  width: 100%;
  height: 100%;
}

.colocar{
  width: 100%;
  padding-top: 100px;
}

.caja{
  position: relative;
  border-radius: 10px;
  background: rgba(255, 255, 255,.65);
  width: 340px;
  height: 364px;
  margin: 0 auto;
  padding-top: 30px;
}
form{
  display: flex;
  flex-direction: column;
}
input[type=name],input[type=password]{
  border: none;
  border-bottom: 1px solid;
  border-radius: 0;
  display: block;
  width: 255px;
  height: 27px;
  margin: 11px auto 0;
}

input[type=checkbox]{
  width: 13px;
}

button{
  background-color: $naranja_iniciarSesion;
  color: $negro;
  border: none;
  width: 112px;
  height: 25px;
  margin: 0 auto;
  font-weight: bold;
}
main p{
  text-align: center;
  margin-top: 20px;
}
h1{
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 19px;
}
form>label{
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 19px;
}
form>label:first-child{
  padding-top: 42px;
}
form>div{
  padding-top: 42px;
  font-size: 13px;
  display: flex;
  justify-content: space-around;
  padding-bottom: 22px;
  align-items: center;
}
input{
  background: none;
}
.botonFormulario{
  background-color: $boton-registrarse;
  color: $blanco;
  border-radius: 5px;
  padding: 9px;
}
.colocar>p{
  font-weight: bold;
}

/*FOOTER*/

/*FOOTER*/

footer{ /*Le indicamos estilos al footer y lo alineamos */
  background-color: $negro;
  color: $blanco;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

footer>*{
  padding: 1.4em;
}


@media screen and (max-width: 1024px) and (min-width: 769px){
  .menu_ordenador li{
    font-size: 12px;
  }
}

/*768-320*/
@media screen and (max-width: 768px) and (min-width: 320px){
  .menu_ordenador{
    display: none;
  }
  .menu_hamburguesa{
    display: block;
  }
  .menu_movil.mostrar {
    display: block;
  }


  footer{
    display: grid;
    text-align: center;
  }

  footer>*{
    padding: 0.4em;
  }

}